@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

* {
  font-family: "Roboto Condensed", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: lightblue;
  background: rgb(248, 244, 245);
  background: linear-gradient(0deg, rgba(248, 244, 245, 0.4963235294117647) 0%, rgba(4, 172, 247, 1) 100%);
  background: rgb(248, 244, 245);
  background: radial-gradient(circle, rgba(248, 244, 245, 0.4963235294117647) 0%, rgba(4, 172, 247, 1) 100%);
  /* background: url(./assets/bg-video.mp4); */
  background: url(./assets/weather-bg.jpg);
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 350px;
  padding: 20px;
  background-color: #fff;
  background: transparent;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.input-container {
  width: 100%;
  display: flex;
  align-items: center;
  border: 1px solid #61dafb;
  border-radius: 10px;
  overflow: hidden;
}

.cityInput {
  flex: 1;
  height: 30px;
  border: none;
  font-size: 16px;
  outline: none;
  padding: 10px;
}

.search-icon {
  padding: 10px;
  height: 30px;
  cursor: pointer;
}

.search-icon img {
  margin-top: 3px;
}

.image {
  text-align: center;
  height: 150px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.image img {
  width: 150px;
  height: 150px;
}

.temp,
.location,
.country {
  margin-top: 5px;
  font-size: 30px;
  color: #333;
  text-transform: uppercase;
  text-align: center;
}

.temp {
  font-weight: 500;
}

.location {
  color: chocolate;
  font-weight: 400;
  font-size: 30px;
}

.country {
  font-size: 18px;
  color: gray;
  font-weight: 500;
}

.cord {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  text-align: center;
}

.cord div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.cord div span {
  font-size: 14px;
}

.air-pressure,
.humidity-percent,
.wind-percent,
.cord div span:nth-child(2) {
  font-size: 18px;
  font-weight: bold;
  color: gray;
  padding-top: 5px;
}

.data-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.element {
  text-align: center;
}

.icon {
  width: 50px;
  height: 50px;
}

.text {
  font-size: 12px;
  color: #888;
}

.copyright {
  text-align: center;
  font-size: 12px;
  color: #888;
  /* font-weight: bold; */
  padding-top: 15px;
}

.copyright span {
  color: #333;
}

.city-not-found,
.loading-message,
.error-message {
  margin-top: 10px;
  color: #888;
  font-size: 25px;
  font-weight: 400;
  text-align: center;
}


@media (max-width:480px) {
  .container {
    width: 300px;
  }
}








.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}